@import url('https://fonts.googleapis.com/css2?family=Jersey+25+Charted&family=Protest+Riot&family=Sedan+SC&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400..700&family=Jersey+25+Charted&family=Protest+Riot&family=Sedan+SC&family=Teko:wght@300..700&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    
}

h1{
    color: #9A3B3B; 
    font-family: "Protest Riot", sans-serif;
    text-align: start !important;
    font-weight: 400;
    font-style: normal;
    font-size: 30px;
    
}

h1.dark{
    color: #f97316; 
    font-family: "Protest Riot", sans-serif;
    font-weight: 400;
    font-style: normal;
    
}

li{
    font-family: "Teko", sans-serif;
    font-size: 22px;
    font-weight: 450;
    letter-spacing: 1px;
    
    display: inline-block;
    margin: 5px 20px;
    cursor: pointer;

}

select{
    background: transparent;
    outline: none;
    border: 0;
    font-family: "Teko", sans-serif;
    font-size: 22px;
    font-weight: 450;
    letter-spacing: 1px;
}




.navbar{
    width: 100%;
    
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #E4C59E;
    /* background-color: #D8AE7E; */
    padding: 10px 20px;
    transition: 0.5s;
    position: sticky;
}

.navbar.dark{
    background: #000;
    color: #fff;
}


ul{
    flex: 1;
    list-style: none;
    text-align: left;
    margin-left: 20px;
}

.search-box{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #F2ECBE;
    padding: 10px 20px;
    border-radius: 50px;
    transition: 0.5s;
}

.search-box.dark{
    background: #fff;
}

.search-box input{
    padding: 6px;
    background: transparent;
    border: 0;
    outline: 0;
    font-size: 18px;
    width: 300px;
    height: 30px;
    
    
}

.search-box.dark input{
    color: #222;
}

.search-box input::placeholder{
    color: #222;

}

.search-box.dark input::placeholder{
    color: #222;

}

.search-box img{
    width: 20px;
    cursor: pointer;
}

.toggle-icon{
    width: 40px;
    cursor: pointer;
    margin-left: 30px;
    margin-right: 70px;
}

.button button{
    border-radius: 50px;
    margin-top: 40%;
    padding: 10px;
    width: 100px;
    height: 40px;
    border: none;
    background-color: #F2ECBE;
    font-size: 16px;
    letter-spacing: .5px;
    cursor: pointer;
    color: black;
}

.button.dark button{
    background-color: #fff;  
}

.button.dark button:hover{
    background-color: #f97316 ;
}

.button button:hover{
    background-color: #C08261;
    color: white;
    border: 1px solid white;
}

.floating-alert {
    position: fixed; 
    top: 10px; 
    left: 50%;
    transform: translateX(-50%); 
    padding: 15px; 
    background-color: #FFE0B5;
    color: #C40C0C; 
    border-radius: 4px; 
    animation: slideIn 0.5s ease forwards; 
    width: 400px;
    height: 75px;
    font-size: 25px;
    border-radius: 20px;

}
  
  @keyframes slideIn {
    0% {
      transform: translate(-50%, -100%); 
        opacity: 0; 
    }
    100% {
      transform: translate(-50%, 0); 
      opacity: 1; 
    }
  }
